/* ==========================================================================
   Elements Burger Menu
   ========================================================================== */

.burger-menu {
  border: 0;
  padding: 0;
  outline: 0;
  background-color: transparent;
  width: 60px;
  height: 60px;
  @include remove-tap-highlight;
  &__bar {
    width: 20px;
    height: 2px;
    background-color: color(black);
    border-radius: 1px;
    display: block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    &:first-child {
      top: 24px;
    }
    &:last-child {
      bottom: 24px;
    }
  }
}
