/* ==========================================================================
   Layout: Category
   ========================================================================== */

.category {
  .container {
    @include medium {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__content {
    background-color: color(white);
    padding: 24px 20px 190px;
  }
  &__title {
    @include font-size(h4);
    margin-bottom: 16px;
  }
  &__data {
    padding-left: 0;
    margin-bottom: 16px;
    list-style: none;
    li {
      @include font-size(h4);
    }
  }
  &__back {
    @include font-size(h4);
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 0;
    font-weight: fw(medium);
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    @include large {
      margin-left: -8px;
      margin-right: -8px;
    }
    @include medium {
      margin-right: -5px;
      margin-left: -5px;
    }
    @include small {
      margin-left: 0;
      margin-right: 0;
    }
  }
  .card-primary,
  .card-tertiary {
    flex: 0 0 33.3333333%;
    padding: 24px 10px;
    min-height: 100%;
    @include large {
      padding: 20px 8px;
    }
    @include medium {
      padding-left: 5px;
      padding-right: 5px;
      flex: 0 0 50%;
    }
    @include small {
      padding: 0;
      flex: 0 0 100%;
      margin-bottom: 48px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
