/* ==========================================================================
   Elements Contacts Form
   ========================================================================== */

.wpcf7 form {
  .wpcf7-not-valid-tip {
    margin-top: 4px;
    @include font-size(h6);
    font-weight: fw(medium);
  }
  .wpcf7-response-output {
    margin: 24px 0 0;
    padding: 0;
    border: 0;
    @include font-size(h5);
    @include small {
      @include font-size(h6);
    }
  }
  .wpcf7-spinner {
    margin: 0;
    opacity: 1;
    display: inline-block;
    vertical-align: middle;
    margin-right: 16px;
  }
}
