/* ==========================================================================
   Variable: Typography
   ========================================================================== */

$font-primary: "Gotham";
$font-icon: "Hirogroup";

// Font
$font: (
  family-primary: $font-primary,
  family-icon: $font-icon,
  h1: (
    font-size: 2.4rem,
    line-height: 2.8rem,
  ),
  h1-a: (
    font-size: 2.2rem,
    line-height: 2.6rem,
  ),
  h1-b: (
    font-size: 2rem,
    line-height: 2.4rem,
  ),
  h2: (
    font-size: 1.8rem,
    line-height: 2.2rem,
  ),
  h2-b: (
    font-size: 1.8rem,
    line-height: 2.5rem,
  ),
  h3: (
    font-size: 1.6rem,
    line-height: 2.2rem,
  ),
  h4: (
    font-size: 1.4rem,
    line-height: 2rem,
  ),
  h5: (
    font-size: 1.2rem,
    line-height: 1.4rem,
  ),
  h5-b: (
    font-size: 1.2rem,
    line-height: 1.6rem,
  ),
  h6: (
    font-size: 1rem,
    line-height: 1.2rem,
  ),
  h6-b: (
    font-size: 1rem,
    line-height: 1.4rem,
  ),
  body: (
    font-size: 1.6rem,
    line-height: 2.2rem,
  ),
  small: (
    font-size: 0.8rem,
    line-height: 1rem,
  ),
  weight: (
    light: 300,
    regular: 400,
    medium: 500,
    bold: 700,
  ),
);
