/* ==========================================================================
   Components Card Box
   ========================================================================== */

.card-box {
  background-color: color(white);
  padding: 20px 20px 20px 30px;
  @include clearfix;
  @include large {
    padding: 20px;
  }
  @include small {
    padding: 10px 10px 20px;
  }
  &__txt {
    width: 41.87%;
    float: left;
    padding-top: 16px;
    padding-right: 32px;
    @include large {
      padding-top: 0;
    }
    @include medium {
      float: none;
      width: 100%;
      padding-right: 0;
      max-width: 80%;
    }
  }
  &__title {
    letter-spacing: 0.1em;
    text-transform: uppercase;
    margin-bottom: 12px;
    @include large {
      @include font-size(h1-b);
    }
    @include medium {
      @include font-size(h2-b);
    }
    @include small {
      @include font-size(h3);
      margin-bottom: 20px;
    }
    @include x-small {
      @include font-size(h4);
    }
  }
  &__desc {
    max-width: 395px;
    @include medium {
      max-width: initial;
    }
    @include small {
      max-width: 320px;
    }
    p, ol, ul {
      margin-bottom: 24px;
      letter-spacing: 0.03em;
      @include font-size(h3);
      @include large {
        @include font-size(h4);
      }
      @include small {
        @include font-size(h5);
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      transition: all .3s ease;
      @include font-size(h3);
      @include large {
        @include font-size(h4);
      }
      @include small {
        @include font-size(h5);
      }
      &:hover {
        color: color(grey, 2);
      }
    }
    table {
      tr, td {
        padding: 8px;
        border: 1px solid color(black);
      }
    }
  }
  &__img {
    width: 58.13%;
    position: relative;
    float: right;
    padding-bottom: 41.727%;
    @include medium {
      width: 100%;
      float: none;
      margin-bottom: 24px;
      padding-bottom: 100%;
    }
    @include small {
      margin-bottom: 16px;
    }
    &__el {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
