/* ==========================================================================
   Mixin Placeholder Shimmer
   ========================================================================== */

@mixin placeholder-shimmer {
  -webkit-mask-image: -webkit-radial-gradient(white, black);
  background: color(grey, 25);
  background-image: linear-gradient(to right, color(grey, 25) 0%, color(grey, 50) 20%, color(grey, 25) 40%, color(grey, 25) 100%);
  background-repeat: no-repeat;
  background-size: 800px 100%;
  animation: placeholderShimmer 1s linear forwards infinite;
}

@mixin placeholder-shimmer-large {
  -webkit-mask-image: -webkit-radial-gradient(white, black);
  background: color(grey, 25);
  background-image: linear-gradient(to right, color(grey, 25) 0%, color(grey, 50) 20%, color(grey, 25) 40%, color(grey, 25) 100%);
  background-repeat: no-repeat;
  background-size: 1500px 100%;
  animation: placeholderShimmerLarge 1.75s linear forwards infinite;
}
