/* ==========================================================================
   Layout: Article
   ========================================================================== */

.article {
  @include medium {
    padding: 20px 20px 40px;
    background-color: color(white);
  }
  @include small {
    padding: 10px 10px 40px;
  }
  .container {
    @include medium {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__wrapper {
    background-color: color(white);
    padding-top: 40px;
    padding-bottom: 50px;
    @include medium {
      padding-top: 0;
      padding-bottom: 0;
      background-color: initial;
    }
  }
  &__content {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }
  &__banner {
    position: relative;
    padding-bottom: 60%;
    margin-bottom: 100px;
    @include large {
      margin-bottom: 80px;
    }
    @include medium {
      margin-bottom: 56px;
    }
    @include small {
      margin-bottom: 32px;
    }
    @include x-small {
      margin-bottom: 18px;
    }
    &__el {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__page {
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: color(grey, 3);
    @include medium {
      @include font-size(h6);
      margin-bottom: 20px;
    }
    @include small {
      @include font-size(small);
    }
  }
  &__title {
    text-transform: uppercase;
    letter-spacing: 0.1em;
    max-width: 532px;
    margin-bottom: 24px;
    @include large {
      @include font-size(h1-b);
    }
    @include medium {
      @include font-size(h2);
      margin-bottom: 20px;
    }
    @include small {
      @include font-size(h3);
    }
    @include x-small {
      @include font-size(h4);
    }
  }
  &__date {
    color: color(grey, 3);
    margin-bottom: 24px;
    font-weight: fw(regular);
    letter-spacing: 0.03em;
    @include medium {
      margin-bottom: 20px;
    }
    @include small {
      @include font-size(h3);
    }
    @include x-small {
      @include font-size(h4);
    }
  }
  &__desc {
    letter-spacing: 0.03em;
    margin-bottom: 80px;
    @include large {

    }
    p, li {
      letter-spacing: 0.03em;
      @include font-size(h2-b);
      @include small {
        @include font-size(h3);
      }
      @include x-small {
        @include font-size(h4);
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      transition: all .3s ease;
      &:hover {
        color: color(grey, 2);
      }
    }
    table {
      tr, td {
        padding: 8px;
        border: 1px solid color(black);
      }
    }
  }
  &__pagination {
    font-size: 0;
    line-height: 1;
    text-align: center;
    @include medium {
      @include clearfix;
    }
    &__left {
      margin-right: 110px;
      @include medium {
        margin-right: 0;
        float: left;
      }
      .fi {
        margin-right: 24px;
        @include large {
          margin-right: 20px;
        }
        @include medium {
          margin-right: 16px;
        }
        @include small {
          margin-right: 12px;
        }
        @include x-small {
          margin-right: 6px;
        }
      }
    }
    &__right {
      @include medium {
        margin-left: 0;
        float: right;
      }
      .fi {
        margin-left: 24px;
        @include large {
          margin-left: 20px;
        }
        @include medium {
          margin-left: 16px;
        }
        @include small {
          margin-left: 12px;
        }
        @include x-small {
          margin-left: 6px;
        }
      }
    }
    a {
      text-decoration: none;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      transition: all .3s ease;
      @include font-size(h5);
      font-weight: fw(medium);
      @include medium {
        @include font-size(h6);
      }
      @include small {
        @include font-size(small);
      }
      &:hover {
        color: color(grey, 2);
      }
      &.disabled {
        color: color(grey, 2);
        cursor: default;
      }
    }
    @include arrow;
  }
}
