/* ==========================================================================
   Variable: Colors
   ========================================================================== */

// colors
$colors: (
  black: #000,
  white: #fff,
  error: #dc3232,
  grey: (
    1: #f2f2f2,
    2: #808080,
    3: #b3b3b3,
    4: #919191,
    25: #f7f7f7,
    50: #e7e7e7,
  ),
  overlay: rgba(#000, 0.7),
  shadow: rgba(#000, 0.1),
  shadow-hover: rgba(#000, 0.2),
  social-media: (
    facebook: #1877f2,
    twitter: #1da1f2,
    instagram: #405de6,
    whatsapp: #25d366,
    youtube: #ff0000,
    spotify: #1db954,
  ),
);
