/* ==========================================================================
   Components Banner
   ========================================================================== */

.banner {
  $b: &;
  &__item {
    position: relative;
  }
  &__title {
    max-width: 620px;
    position: absolute;
    left: 48px;
    color: color(white);
    margin-bottom: 0;
    z-index: zi(banner-title);
    letter-spacing: 0.03em;
    font-weight: fw(regular);
    @include font-size(h1);
    @include large {
      left: 40px;
      @include font-size(h1-a);
    }
    @include medium {
      @include font-size(h1-b);
    }
    br {
      display: none;
      @include medium {
        display: block;
      }
    }
  }
  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 190px;
    height: 190px;
    transform: translate(-50%, -50%);
    z-index: zi(banner-logo);
    @include large {
      width: 164px;
      height: 164px;
    }
    @include medium {
      width: 156px;
      height: 156px;
    }
    @include x-small {
      width: 132px;
      height: 132px;
    }
    @include small {
      width: 124px;
      height: 124px;
    }
    &__el {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__link {
    display: block;
    text-decoration: none;
  }
  &__img {
    position: relative;
    padding-bottom: 49.286%;
    @include medium {
      padding-bottom: 114.93%;
    }
    &::after {
      content: '';
      opacity: 0;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 280px;
      z-index: zi(banner-img-gradient);
    }
    &__el {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: zi(banner-img-el);
      &--d {
        @include medium {
          display: none;
        }
      }
      &--m {
        display: none;
        @include medium {
          display: block;
        }
      }
    }
  }
  /* modifier ----------------------------------------------------------------- */
  &#{$b} {
    /* home --------------------------------------------------------------------- */
    &--home {
      #{$b} {
        &__item {
          @include medium {
            margin-bottom: 10px;
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
        &__title {
          top: 36px;
          letter-spacing: 0.1em;
          text-transform: uppercase;
          font-weight: fw(medium);
          @include large {
            top: 32px;
          }
          @include medium {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            width: 280px;
          }
          @include small {
            width: 248px;
            @include font-size(h2);
          }
          span {
            display: block;
            margin-bottom: 8px;
            @include font-size(h5);
            @include small {
              @include font-size(h6);
            }
          }
        }
        &__img {
          &::after {
            top: 0;
            bottom: initial;
            background: linear-gradient(0deg, rgba(186, 186, 186, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
            @include medium {
              top: initial;
              bottom: 0;
              background: linear-gradient(180deg, rgba(186, 186, 186, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
            }
          }
        }
      }
    }
    /* about -------------------------------------------------------------------- */
    &--about {
      @include medium {
        padding: 20px 20px 40px;
        background-color: color(white);
      }
      @include small {
        padding: 10px 10px 40px;
      }
      #{$b} {
        &__item {
          @include medium {
            margin-bottom: 68px;
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
        &__title {
          bottom: 48px;
          @include medium {
            bottom: initial;
            left: initial;
            position: relative;
            max-width: initial;
            color: color(black);
            transform: none;
          }
          @include small {
            @include font-size(h3);
          }
          @include x-small {
            @include font-size(h4);
          }
        }
        &__img {
          @include medium {
            margin-bottom: 32px;
          }
          @include small {
            margin-bottom: 24px;
          }
          @include x-small {
            margin-bottom: 20px;
          }
          &::after {
            background: linear-gradient(180deg, rgba(186, 186, 186, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
            @include medium {
              display: none;
            }
          }
        }
      }
    }
    /* brands ------------------------------------------------------------------- */
    &--brands {
      #{$b} {
        &__item {
          @include medium {
            margin-bottom: 40px;
            &:last-child {
              margin-bottom: 0;
            }
          }
          @include small {
            margin-bottom: 32px;
          }
          @include x-small {
            margin-bottom: 24px;
          }
          /* logo --------------------------------------------------------------------- */
          &--logo {
            @include medium {
              margin-bottom: 20px;
            }
            @include small {
              margin-bottom: 16px;
            }
            @include x-small {
              margin-bottom: 12px;
            }
          }
          /* txt ---------------------------------------------------------------------- */
          &--txt {
            #{$b}__img {
              @include medium {
                margin-bottom: 24px;
              }
              @include small {
                margin-bottom: 16px;
              }
              @include x-small {
                margin-bottom: 8px;
              }
              &::after {
                background: linear-gradient(180deg, rgba(186, 186, 186, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
                @include medium {
                  display: none;
                }
              }
            }
          }
        }
        &__title {
          bottom: 48px;
          @include medium {
            bottom: initial;
            left: initial;
            position: relative;
            max-width: initial;
            color: color(black);
            transform: none;
          }
          @include small {
            @include font-size(h3);
          }
          @include x-small {
            @include font-size(h4);
          }
        }
      }
    }
  }
}
