/* ==========================================================================
   Mixin Carousel
   ========================================================================== */

@mixin carousel() {
  .owl-carousel {
    .owl-prev,
    .owl-next {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      transition: all .3s ease;
      width: 100px;
      height: 100px;
      @include large {
        width: 80px;
        height: 80px;
      }
      &::before {
        color: color(white);
        font-size: 2.2rem;
        @include large {
          font-size: 2rem;
        }
      }
      span {
        display: none;
      }
      &.disabled {
        cursor: default;
        opacity: 0;
      }
    }
    .owl-prev {
      left: 24px;
      text-align: left;
      @include font-icon(\e901);
      &:hover {
        left: 20px;
      }
      &.disabled {
        left: 16px;
      }
    }
    .owl-next {
      right: 24px;
      text-align: right;
      @include font-icon(\e900);
      &:hover {
        right: 20px;
      }
      &.disabled {
        right: 16px;
      }
    }
  }
}
