/* ==========================================================================
   Components Card Primary
   ========================================================================== */

.card-primary {
  $cp: &;
  &.is-visible {
    #{$cp}__img {
      animation: none;
      background-image: none;
      &__el {
        opacity: 1;
      }
    }
  }
  &__box {
    height: 100%;
    position: relative;
    padding-bottom: 56px;
    @include large {
      padding-bottom: 48px;
    }
    @include medium {
      padding-bottom: 40px;
    }
    @include small {
      padding-bottom: 0;
    }
    &:hover {
      #{$cp} {
        &__img__el {
          transform: scale(1.1);
        }
        &__title {
          color: color(grey, 2);
        }
      }
      .fi-arrow-right {
        color: color(grey, 2);
      }
    }
  }
  &__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: zi(card-primary-link);
  }
  &__img {
    position: relative;
    padding-bottom: 100%;
    margin-bottom: 16px;
    overflow: hidden;
    @include placeholder-shimmer;
    @include large {
      margin-bottom: 12px;
    }
    &__el {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: opacity 0.3s ease, transform 1s ease;
      z-index: zi(card-primary-img-el);
      opacity: 0;
    }
  }
  &__category {
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: color(grey, 3);
    margin-bottom: 12px;
    @include large {
      @include font-size(h6);
      margin-bottom: 8px;
    }
  }
  &__tags {
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: color(grey, 3);
    margin-bottom: 12px;
    @include large {
      @include font-size(h6);
      margin-bottom: 8px;
    }
  }
  &__title {
    max-width: 320px;
    margin-bottom: 12px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    transition: all 0.3s ease;
    @include multiline-ellipsis(3);
    @include large {
      @include font-size(h3);
    }
    @include medium {
      @include font-size(h4);
    }
    @include small {
      max-width: initial;
    }
  }
  &__excerpt {
    margin-bottom: 0;
  }
  &__arrow {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: zi(card-primary-arrow);
    @include small {
      display: none;
    }
  }
  .fi-arrow-right {
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    font-size: 1.2rem;
    transition: all 0.3s ease;
    @include medium {
      font-size: 1rem;
    }
  }
}
