/* ==========================================================================
   Layout: Header
   ========================================================================== */

.header {
  $h: &;
  width: 100%;
  z-index: zi(header);
  background-color: color(grey, 1);
  @include medium {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: zi(header-m);
    transition: all 0.5s ease;
    .scroll-down & {
      transform: translateY(-100%);
    }
    &::before {
      content: "";
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      opacity: 0;
      visibility: hidden;
      background-color: rgba(0, 0, 0, 0.6);
      transition: all 0.75s ease;
      transition-delay: 0.45s;
      z-index: zi(header-overlay);
      .show-navigation & {
        opacity: 1;
        visibility: visible;
        transition-delay: 0s;
      }
    }
  }
  &__inner {
    position: relative;
    padding: 32px 0 18px;
    transition: all 0.3s ease;
    @include medium {
      padding-top: 16px;
      padding-bottom: 12px;
    }
  }
  &__logo {
    position: absolute;
    top: 24px;
    left: 20px;
    z-index: zi(header-logo);
    @include remove-tap-highlight;
    @include medium {
      position: relative;
      top: initial;
      left: initial;
      display: inline-block;
      vertical-align: middle;
    }
    @include small {
      left: -10px;
    }
    &__el {
      display: block;
      @include medium {
        height: 32px;
      }
    }
  }
  &__menu {
    width: 50%;
    margin-left: auto;
    text-align: left;
    @include medium {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 72px 24px;
      transform: scaleY(0);
      transition: all 0.45s ease;
      transform-origin: top center;
      transition-delay: 0.4s;
      z-index: zi(header-menu);
      background-color: color(grey, 2);
      @include scroll("y", "touch", "show");
      .show-navigation & {
        transform: none;
        transition-delay: 0s;
      }
    }
  }
  &__list {
    padding-left: 30px;
    font-size: 0;
    line-height: 1;
    margin-bottom: 0;
    @include medium {
      padding-left: 0;
    }
  }
  &__item {
    display: inline-block;
    vertical-align: top;
    margin-right: 113px;
    position: relative;
    @include mq(1400px) {
      margin-right: 80px;
    }
    @include large {
      margin-right: 64px;
    }
    @include mq(1180px) {
      margin-right: 48px;
    }
    @include medium {
      display: block;
      opacity: 0;
      line-height: 1;
      margin-right: 0;
      padding-top: 16px;
      padding-bottom: 16px;
      transform: translateY(12px);
      transition: all 0.5s ease;
      @for $i from 1 through 4 {
        $delay: 100 - ($i * 30) + ms;
        &:nth-child(#{$i}) {
          transition-delay: $delay;
        }
      }
      .show-navigation & {
        transform: none;
        opacity: 1;
        @for $i from 1 through 4 {
          $delay: 150 + ($i * 50) + ms;
          &:nth-child(#{$i}) {
            transition-delay: $delay;
          }
        }
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    @include small {
      padding-top: 8px;
      padding-bottom: 8px;
    }
    &--dropdown {
      @include medium {
        padding-bottom: 0;
        #{$h} {
          &__link {
            margin-bottom: 16px;
          }
        }
      }
      @include small {
        padding-bottom: 0;
        #{$h} {
          &__link {
            margin-bottom: 8px;
          }
        }
      }
    }
    &:last-child {
      margin-right: 0;
    }
    &--active {
      #{$h} {
        &__link {
          &::after {
            opacity: 1;
          }
        }
      }
    }
    &--show {
      #{$h} {
        &__dropdown {
          opacity: 1;
          visibility: visible;
          transform: none;
        }
      }
    }
  }
  &__dropdown {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    top: 38px;
    left: 0;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    transform: translateY(4px);
    transition: all 0.3s ease;
    width: 180px;
    @include medium {
      display: none;
      transform: none;
      position: relative;
      transition: none;
      top: initial;
      left: initial;
      width: initial;
      opacity: 1;
      visibility: visible;
      padding: 16px 0 16px 32px;
    }
    @include small {
      padding-top: 8px;
      padding-bottom: 8px;
    }
    &__item {
      margin-bottom: 16px;
      line-height: 1;
      font-size: 0;
      @include medium {
        margin-bottom: 32px;
      }
      @include small {
        margin-bottom: 16px;
      }
      &:last-child {
        margin-bottom: 0;
      }
      &--active {
        #{$h} {
          &__dropdown__link {
            &::after {
              opacity: 1;
            }
          }
        }
      }
    }
    &__link {
      display: inline-block;
      vertical-align: middle;
    }
  }
  &__link {
    display: block;
    @include medium {
      display: inline-block;
      vertical-align: middle;
    }
  }
  &__link,
  &__dropdown__link {
    text-decoration: none;
    letter-spacing: 0.12em;
    font-weight: fw(medium);
    text-transform: uppercase;
    transition: all 0.3s ease;
    position: relative;
    @include remove-tap-highlight;
    @include font-size(h5);
    @include medium {
      @include font-size(h1);
      color: color(white);
    }
    @include small {
      @include font-size(h4);
    }
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      border-bottom: 1px solid color(black);
      opacity: 0;
      transition: all 0.3s ease;
      @include medium {
        border-color: color(white);
      }
    }
    &:hover {
      color: color(grey, 2);
      @include medium {
        color: color(white);
      }
      &::after {
        border-color: color(grey, 2);
        @include medium {
          border-color: color(white);
        }
      }
    }
  }
  .burger-menu {
    display: none;
    @include medium {
      position: absolute;
      right: -20px;
      top: 0;
      z-index: zi(burger-menu);
      display: block;
    }
    &__bar {
      transition: all 0.3s ease;
      transition-delay: 0.5s;
      .show-navigation & {
        transition-delay: 0s;
        background-color: color(white);
        &:nth-child(1) {
          top: 29px;
          transform: translateX(-50%) rotate(45deg);
        }
        &:nth-child(2) {
          bottom: 29px;
          transform: translateX(-50%) rotate(-45deg);
        }
      }
    }
  }
}
