/* ==========================================================================
   Layout: Brands
   ========================================================================== */

.brands {
  &__list {
    font-size: 0;
    margin-left: -10px;
    margin-right: -10px;
    @include medium {
      margin-top: -20px;
    }
    @include small {
      margin: 0;
    }
  }
  .container {
    @include medium {
      padding-left: 0;
      padding-right: 0;
    }
  }
  .card-secondary {
    width: 50%;
    display: inline-block;
    vertical-align: top;
    padding: 20px 10px;
    @include small {
      width: 100%;
      padding: 0;
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
