/* ==========================================================================
   Vendor Wordpress
   ========================================================================== */

.wp-block-image,
.wp-block-embed {
  figcaption {
    letter-spacing: 0.03em;
    @include font-size(h2-b);
    @include small {
      @include font-size(h3);
    }
    @include x-small {
      @include font-size(h4);
    }
  }
}

// block-embed
.wp-block-embed {
  &.is-type-video {
    .wp-block-embed__wrapper {
      position: relative;
      overflow: hidden;
      width: 100%;
      padding-top: 56.25%;
      iframe {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        display: block;
      }
    }
  }
}
