/* ==========================================================================
   Elements Animation
   ========================================================================== */

@keyframes placeholderShimmer {
  0% {
    background-position: -468px 0;
  }

  100% {
    background-position: 468px 0;
  }
}

@keyframes placeholderShimmerLarge {
  0% {
    background-position: -1500px 0;
  }

  100% {
    background-position: 1500px 0;
  }
}
