/* ==========================================================================
   Components Card Secondary
   ========================================================================== */

.card-secondary {
  $cs: &;
  &.is-visible {
    #{$cs}__img {
      animation: none;
      background-image: none;
      &__el {
        opacity: 1;
      }
    }
  }
  &__box {
    position: relative;
    &:hover {
      #{$cs} {
        &__img__el {
          transform: scale(1.1);
        }
        &__title {
          color: color(grey, 2);
          @include small {
            color: color(white);
          }
        }
      }
    }
  }
  &__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: zi(card-secondary-link);
  }
  &__img {
    position: relative;
    padding-bottom: 66.667%;
    margin-bottom: 24px;
    overflow: hidden;
    background-size: 1000px 100%;
    @include placeholder-shimmer-large;
    @include large {
      margin-bottom: 20px;
    }
    @include medium {
      margin-bottom: 16px;
    }
    @include small {
      margin-bottom: 0;
      padding-bottom: 114.93%;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 336px;
        z-index: zi(card-secondary-img-gradient);
        background: linear-gradient(
          180deg,
          rgba(186, 186, 186, 0) 0%,
          rgba(0, 0, 0, 0.5) 100%
        );
      }
    }
    &__el {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: zi(card-secondary-img-el);
      transition: opacity 0.3s ease, transform 1s ease;
      opacity: 0;
    }
  }
  &__title {
    margin-bottom: 0;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    padding-left: 20px;
    transition: all 0.3s ease;
    @include font-size(h2);
    @include large {
      padding-left: 16px;
    }
    @include small {
      text-align: center;
      width: 232px;
      color: color(white);
      padding-left: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: zi(card-secondary-title);
      br {
        display: none;
        @include medium {
          display: block;
        }
      }
    }
  }
}
