/* ==========================================================================
   Layout: Banner
   ========================================================================== */

.banner {
  $b: &;
  .container {
    @include medium {
      padding-left: 0;
      padding-right: 0;
    }
  }
  #{$b} {
    &__item {
      @include medium {
        visibility: hidden;
      }
    }
    &__title {
      opacity: 0;
      transform: translateX(-16px);
      transition: all .75s ease;
      transition-delay: 0s;
      @include medium {
        opacity: 1;
        transition: none;
      }
    }
    &__logo {
      opacity: 0;
      transform: translate(-50%, -45%);
      transition: all .75s ease;
      @include medium {
        opacity: 1;
        transition: none;
      }
    }
    &__img {
      overflow: hidden;
      &::after {
        opacity: 0;
        transition: all 3s ease;
        transition-delay: 0s;
        @include medium {
          opacity: 1;
          transition: none;
        }
      }
    }
  }
  #{$b}__list--change {
    #{$b}--single,
    .owl-item:first-child {
      #{$b}__img__el {
        opacity: 1;
      }
    }
  }
  .owl-item:first-child {
    #{$b}__img__el {
      opacity: 0;
      transition: opacity .5s ease;
    }
  }
  &__list--single,
  .owl-item.active {
    #{$b} {
      &__title {
        opacity: 1;
        transform: none;
        transition-delay: .6s;
      }
      &__img {
        &::after {
          opacity: 1;
          transition-delay: .2s;
        }
        &__el {
          opacity: 1;
          transform: none;
          transition-delay: 0s;
        }
      }
      &__logo {
        opacity: 1;
        transform: translate(-50%, -50%);
      }
    }
  }

  @include carousel;
}
