/* ==========================================================================
   Components Career
   ========================================================================== */

.career {
  &__box {
    padding: 30px 30px 20px;
    background-color: color(white);
    @include large {
      padding: 20px;
    }
    @include small {
      padding: 10px;
    }
  }
  &__subtitle {
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: color(grey, 2);
    margin-bottom: 20px;
    @include large {
      margin-bottom: 16px;
    }
    @include medium {
      margin-bottom: 12px;
      @include font-size(h6);
    }
    @include small {
      @include font-size(small);
    }
  }
  &__title {
    letter-spacing: 0.1em;
    text-transform: uppercase;
    margin-bottom: 40px;
    @include large {
      margin-bottom: 32px;
    }
    @include medium {
      margin-bottom: 24px;
      @include font-size(h3);
    }
    @include small {
      margin-bottom: 16px;
      @include font-size(h4);
    }
  }
  &__desc {
    letter-spacing: 0.03em;
    margin-bottom: 48px;
    display: none;
    @include large {
      margin-bottom: 40px;
    }
    @include medium {
      margin-bottom: 32px;
    }
    @include small {
      margin-bottom: 24px;
      max-width: 330px;
    }
    p, ol, ul {
      letter-spacing: 0.03em;
      &:last-child {
        margin-bottom: 0;
      }
      @include medium {
        @include font-size(h4);
      }
      @include small {
        @include font-size(h5);
      }
    }
    a {
      transition: all .3s ease;
      &:hover {
        color: color(grey, 2);
      }
    }
  }
  &__action {
    text-align: right;
    line-height: 1;
    @include medium {
      text-align: left;
    }
    .btn {
      font-weight: fw(medium);
      color: color(grey, 2);
      transition: all .3s ease;
      @include medium {
        @include font-size(h6);
      }
      @include small {
        @include font-size(small);
      }
      &:hover {
        color: color(grey, 3);
      }
    }
  }
}
