/* ==========================================================================
   Elements Button
   ========================================================================== */

.btn {
  border: 0;
  padding: 0;
  outline: 0;
  text-decoration: none;
  background-color: transparent;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  font-weight: fw(medium);
  color: color(black);
  transition: all 0.3s ease;
  @include font-size(h5);
  @include remove-tap-highlight;
  &:hover {
    color: color(grey, 2);
  }
  &--primary {
  }
  &--secondary {
  }
}
