/* ==========================================================================
   Mixin Arrow
   ========================================================================== */

@mixin arrow() {
  .fi {
    font-size: 1.2rem;
    @include medium {
      font-size: 1rem;
    }
    @include small {
      font-size: .8rem;
    }
    @include x-small {
      font-size: .6rem;
    }
  }
}
