/* ==========================================================================
   Layout: Career
   ========================================================================== */

.career {
  $c: &;
  padding-bottom: 200px;
  @include large {
    padding-bottom: 164px;
  }
  @include medium {
    padding-bottom: 120px;
  }
  @include small {
    padding-bottom: 0;
  }
  .container {
    @include medium {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__content {
    @include clearfix;
    margin-left: -10px;
    margin-right: -10px;
    padding-top: 10px;
    @include medium {
      padding-top: 0;
      margin-left: 0;
      margin-right: 0;
    }
  }
  &__list {
    float: left;
    width: 50%;
    @include medium {
      float: none;
      width: 100%;
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    @include small {
      margin-bottom: 10px;
    }
  }
  #{$c} {
    &__item {
      padding: 10px;
      @include medium {
        padding: 0;
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      @include small {
        margin-bottom: 10px;
      }
    }
  }
  .card-box {
    @include medium {
      margin-bottom: 20px;
    }
    @include small {
      margin-bottom: 10px;
    }
  }
}
