/* ==========================================================================
   Grid System
   ========================================================================== */

.container {
  width: 100%;
  max-width: $mw-container;
  margin-left: auto;
  margin-right: auto;
  @include mq(1960px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.main-site {
  width: 100%;
  padding-top: 16px;
  position: relative;
  z-index: zi(main-site);
  @include medium {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 60px;
  }
  @include small {
    padding-left: 10px;
    padding-right: 10px;
  }
}
