/* ==========================================================================
   Vendor Mailchimp
   ========================================================================== */

.footer {
  #mc_embed_signup_scroll {
    position: relative;
  }
  #mc_embed_signup input.mce_inline_error {
    border-color: color(black);
  }
  #mc_embed_signup div.mce_inline_error,
  #mce-error-response,
  #mce-success-response {
    font-weight: fw(medium);
    position: absolute;
    left: 0;
    top: 28px;
    background-color: transparent;
    @include font-size(h6);
    padding-right: 56px;
    @include small {
      padding-right: 24px;
      position: relative;
      top: -12px;
    }
  }
  #mc_embed_signup div.mce_inline_error {
    margin-bottom: 0;
    padding: 0;
    color: color(error);
  }
  #mce-error-response {
    color: color(error);
  }
}
