/* ==========================================================================
   Components Card Tertiary
   ========================================================================== */

.card-tertiary {
  $ct: &;
  &.is-visible {
    #{$ct}__img {
      animation: none;
      background-image: none;
      &__el {
        opacity: 1;
      }
    }
  }
  &__box {
    height: 100%;
    position: relative;
    &:hover {
      #{$ct} {
        &__img__el,
        &__video__el {
          transform: scale(1.1);
        }
      }
    }
  }
  &__link {
    padding: 0;
    border: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: zi(card-tertiary-link);
  }
  &__img {
    position: relative;
    padding-bottom: 100%;
    margin-bottom: 20px;
    overflow: hidden;
    @include placeholder-shimmer;
    &__el {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: zi(card-tertiary-img-el);
      transition: opacity 0.3s ease, transform 1s ease;
      opacity: 0;
    }
  }
  &__video {
    position: relative;
    padding-bottom: 100%;
    margin-bottom: 20px;
    overflow: hidden;
    &__el {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: zi(card-tertiary-img-el);
      transition: all 1s ease;
      opacity: 1;
    }
  }
  &__category {
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: color(grey, 3);
    margin-bottom: 0;
  }
}
