/* ==========================================================================
   Layout: Footer
   ========================================================================== */

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-top: 40px;
  padding-bottom: 36px;
  z-index: zi(footer);
  background-color: color(grey, 1);
  @include small {
    position: relative;
    padding-bottom: 20px;
  }
  &__wrapper {
    @include clearfix;
  }
  &__left {
    float: left;
    width: 50%;
    text-transform: uppercase;
    @include medium {
      width: 40%;
    }
    @include small {
      width: 100%;
      float: none;
      margin-bottom: 48px;
    }
  }
  &__right {
    float: right;
    width: 50%;
    padding-left: 30px;
    @include medium {
      width: 60%;
      padding-left: 24px;
    }
    @include small {
      width: 100%;
      float: none;
      padding-left: 0;
    }
  }
  &__col {
    display: inline-block;
    vertical-align: top;
    margin-right: 48px;
    @include large {
      margin-right: 40px;
    }
    @include medium {
      margin-right: 32px;
      max-width: 120px;
    }
    @include small {
      margin-right: 24px;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__title {
    font-weight: fw(medium);
    color: color(grey, 4);
    letter-spacing: 0.1em;
    margin-bottom: 36px;
    @include large {
      margin-bottom: 32px;
    }
    @include medium {
      margin-bottom: 24px;
      @include font-size(small);
    }
    @include small {
      margin-bottom: 20px;
    }
  }
  &__menu {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    &__item {
      line-height: 1;
      margin-bottom: 12px;
      @include x-small {
        margin-bottom: 8px;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    &__link {
      @include font-size(h5);
      font-weight: fw(medium);
      text-decoration: none;
      letter-spacing: 0.1em;
      transition: all .3s ease;
      @include medium {
        @include font-size(h6);
      }
      &:hover {
        color: color(grey, 2);
      }
    }
  }
  &__desc {
    max-width: 420px;
    margin-bottom: 48px;
    @include large {
      margin-bottom: 42px;
    }
    @include medium {
      margin-bottom: 36px;
    }
    @include small {
      margin-bottom: 24px;
      max-width: 240px;
    }
    p {
      @include font-size(h5-b);
      letter-spacing: 0.03em;
      margin-bottom: 0;
      @include medium {
        @include font-size(h6-b);
      }
    }
  }
  &__newsletter {
    max-width: 372px;
    width: 100%;
    margin-bottom: 96px;
    @include large {
      margin-bottom: 80px;
    }
    @include medium {
      margin-bottom: 56px;
    }
    @include small {
      margin-bottom: 24px;
      max-width: 304px;
    }
    @include x-small {
      margin-bottom: 16px;
    }
    &__title {
      text-transform: uppercase;
      letter-spacing: 0.1em;
      margin-bottom: 20px;
      @include medium {
        margin-bottom: 16px;
        @include font-size(h6);
      }
      @include small {
        margin-bottom: 12px;
      }
    }
    .hirogroup-input {
      margin-bottom: 12px;
      @include medium {
        @include font-size(h6);
      }
      @include small {
        padding-bottom: 8px;
      }
    }
    .hirogroup-submit {
      display: block;
      margin-left: auto;
    }
  }
  &__bottom {
    max-width: 648px;
    @include clearfix;
    @include small {
      position: relative;
    }
    &__link {
      float: left;
      width: 33.3333333%;
      text-decoration: none;
      text-transform: uppercase;
      font-weight: fw(medium);
      letter-spacing: 0.1em;
      @include font-size(h5);
      transition: all .3s ease;
      @include medium {
        width: 40%;
        @include font-size(h6);
      }
      @include small {
        width: 100%;
        margin-bottom: 12px;
      }
      &:hover {
        color: color(grey, 2);
      }
    }
  }
  &__copyright {
    margin-bottom: 0;
    letter-spacing: 0.1em;
    text-align: center;
    float: left;
    width: 33.3333333%;
    @include medium {
      width: 40%;
      text-align: left;
      @include font-size(h6);
    }
    @include small {
      width: 100%;
    }
  }
  &__socmed {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    font-size: 0;
    line-height: 1;
    text-align: right;
    float: left;
    width: 33.3333333%;
    @include medium {
      width: 20%;
    }
    @include small {
      width: initial;
      position: absolute;
      bottom: 0;
      right: 0;
    }
    &__item {
      display: inline-block;
      vertical-align: middle;
      margin-right: 24px;
      transition: all .3s ease;
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        transform: translateY(-2px);
        color: color(grey, 2);
      }
    }
    &__link {
      text-decoration: none;
    }
    .fi {
      font-size: 1.2rem;
      display: block;
    }
  }
}
