/* ==========================================================================
   Layout: Section Primary
   ========================================================================== */

.section-primary {
  $sp: &;
  @include mq(1960px) {
    padding-left: 20px;
    padding-right: 20px;
  }
  @include medium {
    padding-left: 0;
    padding-right: 0;
  }
  @include small {
    padding: 16px 10px;
  }
  .container {
    background-color: color(white);
    padding: 0 20px 32px;
    min-height: 540px;
    @include large {
      padding-bottom: 28px;
    }
    @include medium {
      min-height: 480px;
      padding-bottom: 24px;
    }
    @include small {
      padding: 0;
      min-height: 320px;
      background-color: initial;
    }
  }
  &__content {
    position: relative;
    margin-bottom: 40px;
    @include large {
      margin-bottom: 32px;
    }
    @include medium {
      margin-bottom: 24px;
    }
  }
  &__aside {
    position: absolute;
    width: 232px;
    left: 0;
    top: 24px;
    z-index: zi(section-primary-aside);
    @include large {
      top: 20px;
      width: 210px;
    }
    @include medium {
      width: 186px;
    }
    @include small {
      display: none;
    }
  }
  &__title {
    @include font-size(h5);
    margin-bottom: 0;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
  &__action {
    display: none;
    text-align: center;
    &__btn {
      &:disabled {
        color: color(grey, 2);
      }
      @include medium {
        @include font-size(h6);
      }
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    @include large {
      margin-left: -8px;
      margin-right: -8px;
    }
    @include medium {
      margin-right: -5px;
      margin-left: -5px;
    }
    @include small {
      margin-left: 0;
      margin-right: 0;
    }
  }
  &__category {
    padding-left: 0;
    margin-top: 8px;
    margin-bottom: 0;
    list-style: none;
    li {
      margin-bottom: 0px;
      font-size: 14px;
    }
    a {
      display: block;
      text-decoration: none;
      font-size: 14px;
    }
  }
  &__tags {
    padding-left: 0;
    margin-top: 8px;
    margin-bottom: 0;
    list-style: none;
    li {
      margin-bottom: 0px;
      font-size: 14px;
    }
    a {
      display: block;
      text-decoration: none;
      font-size: 14px;
    }
  }
  .card-primary,
  .card-tertiary {
    flex: 0 0 33.3333333%;
    padding: 24px 10px;
    min-height: 100%;
    @include large {
      padding: 20px 8px;
    }
    @include medium {
      padding-left: 5px;
      padding-right: 5px;
      flex: 0 0 50%;
    }
    @include small {
      padding: 0;
      flex: 0 0 100%;
      margin-bottom: 48px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .article {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }
  &#{$sp}--event {
    #{$sp} {
      &__content {
        margin-bottom: 0;
        padding-left: 30%;
        @include small {
          padding-left: 0;
        }
      }
    }
  }
  &#{$sp}--news {
    #{$sp} {
      &__content {
        padding-left: 258px;
        @include large {
          padding-left: 226px;
        }
        @include medium {
          padding-left: 194px;
        }
        @include small {
          padding-left: 0;
        }
      }
    }
  }
  .data-not-found {
    padding-top: 24px;
    @include small {
      padding-top: 0;
    }
    p {
      @include font-size(h5);
      margin-bottom: 0;
      letter-spacing: 0.1em;
      text-transform: uppercase;
    }
  }
}
