/* ==========================================================================
   Layout: Contacts
   ========================================================================== */

.contacts {
  .container {
    @include medium {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__wrapper {
    @include clearfix;
    background-color: color(white);
    padding: 30px 30px 190px;
    @include large {
      padding-bottom: 172px;
    }
    @include medium {
      padding: 20px 20px 156px;
    }
    @include small {
      padding: 16px 10px 20px;
    }
  }
  &__left {
    float: left;
    width: 50%;
    @include medium {
      width: 40%;
    }
    @include small {
      float: none;
      width: 100%;
      margin-bottom: 56px;
    }
    p {
      letter-spacing: 0.03em;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a, p, li {
      @include medium {
        @include font-size(h4);
      }
      @include small {
        @include font-size(h5-b);
      }
    }
  }
  &__right {
    width: 50%;
    max-width: 372px;
    float: left;
    padding-left: 30px;
    @include medium {
      width: 60%;
      padding-left: 24px;
    }
    @include small {
      width: 100%;
      float: none;
      padding-left: 0;
      max-width: 304px;
    }
  }
  h5 {
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-bottom: 28px;
    @include large {
      margin-bottom: 24px;
    }
    @include medium {
      @include font-size(h6);
      margin-bottom: 20px;
    }
    @include small {
      @include font-size(small);
      margin-bottom: 16px;
    }
  }
  &__content {
    a {
      text-decoration: none;
      transition: all .3s ease;
      letter-spacing: 0.03em;
      &:hover {
        color: color(grey, 2);
      }
    }
    p {
      margin-bottom: 0;
      letter-spacing: 0.03em;
    }
    a, p, li {
      @include medium {
        @include font-size(h4);
      }
      @include small {
        @include font-size(h5-b);
      }
    }
  }
  &__form {
    margin-top: 72px;
    @include large {
      margin-top: 64px;
    }
    @include medium {
      margin-top: 56px;
    }
    @include small {
      margin-top: 32px;
    }
    @include x-small {
      margin-top: 24px;
    }
    textarea {
      resize: none;
      height: 158px;
      @include large {
        height: 140px;
      }
      @include medium {
        height: 124px;
      }
      @include small {
        height: 100px;
      }
    }
    &__submit {
      position: relative;
      text-align: right;
    }
    &__item {
      margin-bottom: 30px;
      @include medium {
        margin-bottom: 24px;
      }
      &:last-child {
        margin-bottom: 20px;
      }
      &:nth-child(3) {
        @include small {
          margin-bottom: 14px;
        }
      }
    }
  }
}
