/* ==========================================================================
   Elements Icons
   ========================================================================== */

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'Hirogroup' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fi-next:before {
  content: "\e900";
}
.fi-prev:before {
  content: "\e901";
}
.fi-arrow-right:before {
  content: "\ea34";
}
.fi-arrow-left:before {
  content: "\ea38";
}
.fi-fb:before {
  content: "\ea90";
}
.fi-ig:before {
  content: "\ea92";
}
