/* ==========================================================================
   Layout: Popup
   ========================================================================== */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 11;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease;
  min-height: 100%;
  cursor: pointer;
}
.popup.show-popup {
  opacity: 1;
  visibility: visible;
}
.popup__link-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.popup__link {
  position: relative;
  text-transform: uppercase;
  transition: all 0.3s ease;
  letter-spacing: 0.12em;
  font-weight: 500;
  color: #fff;
  z-index: 1;
  text-decoration: none;
  margin-top: 8px;
  display: inline-block;
  vertical-align: middle;
  font-size: 1.2rem;
  line-height: 1.4rem;
}
.popup__link:hover {
  opacity: 0.8;
}
.popup__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid #fff;
  transition: all 0.3s ease;
}
.popup__inner {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  width: 100%;
  max-width: 1280px;
  padding-top: 32px;
  padding-bottom: 32px;
  align-content: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
@media only screen and (max-width: 767.98px) {
  .popup__inner {
    padding-top: 24px;
    padding-bottom: 24px;
  }
}
.popup__img, .popup__video {
  flex: 0 0 100%;
  text-align: center;
  height: 98%;
  position: relative;
}
.single-event_promotion .popup__img, .single-event_promotion .popup__video {
  z-index: 2;
}
@media only screen and (max-width: 991.98px) {
  .popup__img, .popup__video {
    width: 100%;
    height: initial;
  }
}
.popup__img__el, .popup__video__el {
  cursor: default;
  height: 100%;
  object-fit: contain;
  z-index: 1;
}
@media only screen and (max-width: 991.98px) {
  .popup__img__el, .popup__video__el {
    width: 100%;
    max-height: 90vh;
  }
}

