/* ==========================================================================
   Layout: Popup
   ========================================================================== */

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: zi(popup);
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease;
  min-height: 100%;
  cursor: pointer;
  &.show-popup {
    opacity: 1;
    visibility: visible;
  }
  &__link-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
  &__link {
    position: relative;
    text-transform: uppercase;
    transition: all 0.3s ease;
    letter-spacing: 0.12em;
    font-weight: fw(medium);
    color: color(white);
    z-index: zi(popup-link);
    text-decoration: none;
    margin-top: 8px;
    display: inline-block;
    vertical-align: middle;
    @include font-size(h5);
    &:hover {
      opacity: 0.8;
    }
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      border-bottom: 1px solid color(white);
      transition: all 0.3s ease;
    }
  }
  &__inner {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    width: 100%;
    max-width: 1280px;
    padding-top: 32px;
    padding-bottom: 32px;
    align-content: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    @include small {
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }
  &__img,
  &__video {
    flex: 0 0 100%;
    text-align: center;
    height: 98%;
    position: relative;
    .single-event_promotion & {
      z-index: zi(popup-detail);
    }
    @include medium {
      width: 100%;
      height: initial;
    }
    &__el {
      cursor: default;
      height: 100%;
      object-fit: contain;
      z-index: zi(popup-img-el);
      @include medium {
        width: 100%;
        max-height: 90vh;
      }
    }
  }
}
